@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&family=Open+Sans&display=swap);
:root {

  --background-image: url('');
  --background-image-small: url('');
  --color-background: #fafce0;
  --color-surface: #edf0be;
  --color-on-background: #202020;
  --color-on-background-shadow: #ffffff83;

  --font-main: 'Ubuntu', sans-serif;
  --font-secondary:  'Open Sans', sans-serif;
  --font-3: 'Comfortaa', cursive;

  --color-dark: #0280906b;
  --color-light: #21a5b69c;
  --color-on-dark-light: #ffffffb2;
  
  --color-primary: #05668D;
  --color-secondary: #21a5b6a9;
  --color-tertiary: #313131;
  
  --color-tertiary-shadow: #ffffff80;

  --color-button: #05668D;
  --color-on-button: #ffffff;
  --color-button-hover: #05668db4;
  --color-button-active: #05668d67;

  --color-on-surface: #2b2b2b;
  --color-on-primary: #1d1d1d;
  --color-on-secondary: #000000;

  --cursor-timer: url(/static/media/timer-white-24dp.edebe58f.svg), auto;
  --cursor-timer-add: url(/static/media/alarm_add-white-24dp.c32e8f4c.svg), auto;
  --cursor-timer-remove: url(/static/media/timer_off-white-24dp.6660f3ce.svg), auto;

  --moon-i: 5;
}

* {
  box-sizing: border-box;
}
body, html {
  padding: 0;
  margin: 0;
  
}
body {
  background: #fafce0;
  background: var(--color-background);
  background-image: url('');
  background-image: var(--background-image-small);
  background-size: cover;
  background-position: center bottom;
  background-attachment: fixed;
  overflow: hidden;
}
.full-background::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-image: url('');
  background-image: var(--background-image);
  background-size: cover;
  background-position: center bottom;
  background-attachment: fixed;
  overflow: hidden;
  -webkit-animation: sharpen 0.3s ease-out;
          animation: sharpen 0.3s ease-out;
}
.blur::before {
  -webkit-filter: blur(30px);
          filter: blur(30px);
  -webkit-animation: blur 0.3s ease-in;
          animation: blur 0.3s ease-in;
}

@-webkit-keyframes sharpen {
  from {
      -webkit-filter: blur(30px);
              filter: blur(30px);
  }
  to {
      -webkit-filter: blur(0px);
              filter: blur(0px);
  }
}

@keyframes sharpen {
  from {
      -webkit-filter: blur(30px);
              filter: blur(30px);
  }
  to {
      -webkit-filter: blur(0px);
              filter: blur(0px);
  }
}

@-webkit-keyframes blur {
  from {
      -webkit-filter: blur(0px);
              filter: blur(0px);
  }
  to {
      -webkit-filter: blur(30px);
              filter: blur(30px);
  }
}

@keyframes blur {
  from {
      -webkit-filter: blur(0px);
              filter: blur(0px);
  }
  to {
      -webkit-filter: blur(30px);
              filter: blur(30px);
  }
}


