/* Sunhours */
.c-sun-hours {
    width: 100%;
    max-width: 600px;
}
.sun-hours__hours {
    width: 100%;
    height: 45px;
    position: relative;
}

.sun-hours__hours__hour {
    display: inline-block;
    position: absolute;
    font-family: var(--font-main);
    color: var(--color-on-background);
    font-weight: 500;
    text-shadow: var(--color-on-background-shadow) 1px 1px 4px;
    font-size: 13px;
    text-align: center;
    float: left;
    width: 50px;
    height: 40px;
    margin-left: -25px;
}
.sun-hours__hours__hour .icon{
    margin-bottom: 4px;
    opacity: 0.6;
    color: var(--color-on-background);
    /* text-shadow: var(--color-on-background-shadow) 1px 1px 4px; */
}

.sun-hours__night-bar {
    height: 20px;
    background-color: var(--color-dark);
    width: 100%;
    position: relative;
    border-radius: 10px;
    margin: auto;
}
.sun-hours__night-bar__day-curve {
    position: absolute;
    z-index: 3;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
.sun-hours__night-bar__now-bar {
    
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    border-top: 1px solid var(--color-on-dark-light);
    background: var(--color-on-dark-light);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    /*z-index: 1;
    height: 100%;
