    */
}
.sun-hours__night-bar__now-bar__fill {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background-color: var(--color-on-dark-light);

}
.sun-hours__night-bar__day-bar {
    height: 100%;
    display: inline-block;
    position: absolute;
    float: left;
    background-color: var(--color-light);
}
.sun-hours__container-lines {
    height: 10px;
    margin-bottom: 4px;
    width: 100%;
    position: relative;
    margin: auto;
}
.sun-hours__container-lines__lines {
    height: 100%;
    display: inline-block;
    position: absolute;
    float: left;
    border-right: 1px var(--color-on-background) dotted;
    border-left: 1px var(--color-on-background) dotted;
}

.sun-hours__container-lines__now-line {
    height: 50%;
    
    width: 1px;
    display: inline-block;
    
    border-right: 2px #051d27 solid;
    position: absolute;
    float: left;
    bottom: 0;
    width: 0; 
    height: 0; 
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 8px solid #051d27;
}

